@font-face {
  font-family: Lovelo;
  src: url('./LoveloBlack.otf');
}

@font-face {
  font-family: Lato;
  src: url('./Lato-Black.ttf');
}

h1 {
  font: 'Lovelo';
  font-size: 60px;
}

.gradient {
  background: linear-gradient(to right, #cd2c2c, #2672ff,#dbbd24, #de4ede);
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
}

.gradient-dark {
  background: linear-gradient(to right, #6930c3, #5e60ce,#5390d9, #4ea8de);
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
}

.topnav {
  background-color: black;
  overflow: hidden;
}

.topnav a {
  float: left;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
  font-weight: 700;
}

.topnav a:hover {
  background: linear-gradient(to right, #6930c3, #5e60ce,#5390d9, #4ea8de);
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
  background-color: #ddd;
  color: black;
}

.topnav a.active {
  background: linear-gradient(to right, #6930c3, #5e60ce,#5390d9, #4ea8de);
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
  background-color: #04AA6D;
  color: white;
}

.App {
  text-align: center;
  max-width: 100%;
  width: 100%;
  overflow: hidden
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
